import telegram from './images/telegram.png'
import facebook from './images/facebook.png'
import instagram from './images/instagram.png'



const footer = () => {
  return (
    <footer>
      <div class="container py-2">
        <div class="row py-4">        
          <div className="col-lg-6 col-md-6 mb-lg-0">
            <h2>Dotflix.</h2>
          </div>
          <div className="col-lg-6 col-md-6 mb-lg-0" style={{ textAlign: "center" }}>

            <a className="btn btn-outline-light btn-floating m-1 " href="#" role="button"
            ><img src={facebook} alt="facebook" /></a>

            <a className="btn btn-outline-light btn-floating m-1" href="#" role="button"
            ><img src={telegram} alt="telegram" /></a>

            <a className="btn btn-outline-light btn-floating m-1" href="#" role="button"
            ><img src={instagram} alt="instagram" /></a>
            <p class="font-italic mt-3">Xidmətimizi indi daha da genişləndirdik. Bizi sosial şəbəkələrdə də izləyin.</p>
            <section className="mb-4">
            </section>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default footer;
