import { NavLink } from 'react-router-dom'
import React from 'react'
import logo from './images/logo.png'
import hamburger from './images/hamburger.png'


const Navbar = () => {
    function click() {
        document.querySelector(".menu").classList.remove("show")
    }
   
    return (
        <div className="App Navbar">
            <nav class="navbar navbar-expand-lg navbar-light bg-dark">
                <div class="container">
                    <div className="logo-navbar">
                        <NavLink to="/dotflix"><img src={logo} alt="logo" /></NavLink>
                        <NavLink to="/dotflix"><a class="navbar-brand" href="#">Dotflix</a></NavLink>
                    </div>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <img style={{ width: "20px" }} src={hamburger} alt="menu" />
                    </button>
                    <div class="collapse navbar-collapse menu" id="navbarSupportedContent">
                        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                            <li class="nav-item">
                                <NavLink to="/movies"><a class="nav-link active" aria-current="page" onClick={click}>Filmlər</a></NavLink>
                                
                            </li>
                            <li class="nav-item">
                                <NavLink  to="/series"><a class="nav-link"onClick={click}>Seriallar</a></NavLink>
                                
                            </li>
                            <li class="nav-item">
                                <NavLink  to="/blog"><a class="nav-link"onClick={click}>Bloq</a></NavLink>
                            </li>
                        </ul>
                        <form class="d-flex">
                            <input class="form-control me-2" type="search" placeholder="Filmin adı.." aria-label="Search" />
                            <button class="form-button btn btn-outline-light" type="submit">Axtar</button>
                        </form>
                    </div>
                </div>
            </nav>
        </div>
    )
}

export default Navbar
