import $ from 'jquery';
import CommonProps from './CommonProps'
import SeriesData from './Data/SeriesData'
import AOS from 'aos';
import 'aos/dist/aos.css';


const Series = () => {
    $(document).ready(function () {

        $(".filter-button").click(function () {
            var value = $(this).attr('data-filter');

            if (value == "all") {
                $('.filter').show('1000');
            }
            else {
                $(".filter").not('.' + value).hide('3000');
                $('.filter').filter('.' + value).show('3000');

            }
        });

        if ($(".filter-button").removeClass("active")) {
            $(this).removeClass("active");
        }
        $(this).addClass("active");

    });
    AOS.init();
    return (
        <div className="App">
            <div className="welcome3">
                <h2 data-aos="fade-right" data-aos-duration="1000">Seriallarımız bir klik uzaqlığınızda!</h2>
            </div>
            <div class="container">
                <div class="row">
                    <div className="col-lg-2 col-sm-3 col-md-3 col-3">
                        <div className="mt-5 filter-button-keeper" data-aos="fade-up" data-aos-duration="3000">
                            <h5 class="filter-button" data-filter="all">Hamısı</h5>
                            <h5 class="filter-button" data-filter="criminal">Kriminal</h5>
                            <h5 class="filter-button" data-filter="action">Aksiyon</h5>
                            <h5 class="filter-button" data-filter="history">Tarixi</h5>
                            <h5 class="filter-button" data-filter="dram">Drama</h5>
                            <h5 class="filter-button" data-filter="war">Savaş</h5>
                            <h5 class="filter-button" data-filter="horror">Qorxu</h5>
                            <h5 class="filter-button" data-filter="thriller">Triller</h5>
                            <h5 class="filter-button" data-filter="mystery">Sirli</h5>
                            <h5 class="filter-button" data-filter="western">Vestern</h5>
                            <h5 class="filter-button" data-filter="thriller">Musiqi</h5>
                            <h5 class="filter-button" data-filter="sci-fi">Elmi-Fantastika</h5>
                            <h5 class="filter-button" data-filter="thriller">Siyasət</h5>
                            <h5 class="filter-button" data-filter="thriller">Romantika</h5>
                            <h5 class="filter-button" data-filter="thriller">Macəra</h5>
                            <h5 class="filter-button" data-filter="thriller">Biyoqrafiya</h5>
                        </div>
                    </div>
                    <div className="col-lg-10 col-sm-9 col-md-9 col-9">
                        <div className="row mt-5" data-aos="fade-up" data-aos-duration="3000">
                            {SeriesData.map((val, ind) => {
                                return <CommonProps key={ind} img={val.img} link={val.link} title={val.title} genre={val.genre} class={val.class} imdb={val.imdb} length={val.length} year={val.year} trailer={val.trailer} />
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Series;
