import BlogProps from './BlogProps'
import SurvivorData from './Data/SurvivorData'
import AOS from 'aos';
import 'aos/dist/aos.css';

const survivor = () => {
    AOS.init();
    return (
        <div className="App Motivation">
            <div className="container">
                <div className="info py-5" data-aos="fade-up" data-aos-duration="2000">
                    <h3 className="top-info">Sağ Qalmaq uğrunda seçdiyimiz ən yaxşı filmlər.</h3>
                    <p>Sağ qalma hekayələrindən daha yaxşısı varsa o da əsl yaşanmış sağ qalma hekayələridir. Dünya təhlükəli bir yerdir, ancaq çətinliklərdən xilas olan insanların inanılmaz hekayələri var. Batan gəmilərdən tutmuş tsunamilərdən və vəhşi mühitdə sağ qalmağa çalışmağa qədər yüzlərcə hadisələrə filmlər çəkilmişdir. Bunlar içindən seçdiklərimizi sizinlə paylaşırıq. Xoş izləmələr!</p>
                </div>
                <div className="row" data-aos="zoom-in-up" data-aos-duration="2000">
                    {SurvivorData.map((val, ind) => {
                        return <BlogProps key={ind} title={val.title} about={val.about} imdb={val.imdb} img={val.img} />
                    })}
                </div>
            </div>
        </div>
    )
}

export default survivor;
