import more from './images/plus.png'
import AOS from 'aos';
import 'aos/dist/aos.css';


const CommonProps = (props) => {
    AOS.init();
    return (
        <div class={props.class}>
            <div class="card">
                <a href={props.link} class="card__cover">
                    <img src={props.img} alt="" />
                    <div className="dropdown plus ">
                        <div id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                            <img src="https://www.themoviedb.org/assets/2/v4/glyphicons/basic/glyphicons-basic-947-circle-more-white-4c440dfc1b0e626c70f4853dbbce9c4d1f2c5d8f3e05a7d3df47881cbd816adf.svg" alt="more" />
                        </div>
                        <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1" data-aos="fade-left" data-aos-duration="1000">
                            <li className="dropdown-item">il: {props.year}</li>
                            <li className="dropdown-item">imdb: {props.imdb}</li>
                            <li className="dropdown-item">müddət: {props.length}</li>
                            <li className="last-item"><a href={props.trailer} className="trailer">Fraqmanı izlə</a></li>
                        </ul>
                    </div>
                </a>
                <h3 class="card__title"><a style={{cursor:"pointer"}}>{props.title}</a></h3>
                <div class="card__list">
                    <p>{props.genre}</p>
                </div>
            </div>
        </div>
    )
}

export default CommonProps;
