import React from 'react'

const BlogProps = (props) => {
    return (
        <div className="col-md-6 mb-5 Blog" style={{maxWidth:"900px",margin:""}}>
            <h2> {props.title}</h2>
            <p>{props.about}</p>
            <p className="imdb">imdb: {props.imdb}</p>
            <div className="blog-img">
                <img src={props.img} alt="img" />
            </div>
        </div>
    )
}

export default BlogProps;
