import { NavLink } from 'react-router-dom'
import AOS from 'aos';
import 'aos/dist/aos.css';

const blog = () => {
    AOS.init();
    return (
        <div className="App Blog">
            <div className="welcome4">
                <h1 data-aos="fade-up" data-aos-duration="1000">Bloq səhifəmizə xoş gəlmisiniz!</h1>
                <h2 data-aos="fade-up" data-aos-duration="2000">Sizin üçün bir-birindən fərqli və maraqlı film siyahıları mövcuddur!</h2>
            </div>
            <div className="container py-5">
                <div className="row">
                    <div className="col-md-6" data-aos="fade-up" data-aos-duration="1000">
                        <NavLink to="/motivation"><h3 className="top-info mt-3">Motivasiyanızı artıracaq 8 film</h3></NavLink>
                        <NavLink to="/survivor"><h3 className="top-info mt-3">"Sağ qalmaq uğrunda" filmlər</h3></NavLink>
                        <h3 className="top-info mt-3">Soyuq havalarda izləniləsi filmlər</h3>
                        <h3 className="top-info mt-3">Zamanda Səyahət mövzulu 10 film</h3>
                        <h3 className="top-info mt-3">Mafiya mövzulu filmlər</h3>
                        <h3 className="top-info mt-3">Yeni il ilə bağlı filmlər</h3>
                        <h3 className="top-info mt-3 mb-3" style={{ marginBottom: "0" }}>Son 20 ilin ən yaxşı filmləri</h3>
                    </div>
                    <div className="col-md-6" data-aos="fade-up" data-aos-duration="2000">
                        <div id="carouselExampleIndicators" class="carousel slide" data-bs-ride="carousel">
                            <div class="carousel-indicators">
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="0" class="active" aria-current="true" aria-label="Slide 1"></button>
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="1" aria-label="Slide 2"></button>
                                <button type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide-to="2" aria-label="Slide 3"></button>
                            </div>
                            <div class="carousel-inner">
                                <div class="carousel-item active">
                                    <img src="https://img-s1.onedio.com/id-546cc1781561ba6e0dec1f82/rev-0/w-635/listing/f-jpg-webp/s-9793a1c58526ba3bba08b462e7c3d554b481ae07.webp" class="d-block w-100" alt="..." />
                                </div>
                                <div class="carousel-item">
                                    <img src="https://img-s2.onedio.com/id-546a28ce3c4309673c72ea49/rev-0/w-635/listing/f-jpg-webp/s-155f8dfa1de4b2a6b31d3ab11d5588f2c91d1260.webp" class="d-block w-100" alt="..." />
                                </div>
                                <div class="carousel-item">
                                    <img src="https://img-s2.onedio.com/id-546ccd9c757c14082049a3cf/rev-0/w-635/listing/f-jpg-webp/s-768b967f87ba9143553875df4878db77465f2311.webp" class="d-block w-100" alt="..." />
                                </div>
                            </div>
                            <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                                <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                <span class="visually-hidden">Previous</span>
                            </button>
                            <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                <span class="visually-hidden">Next</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default blog;
