import BlogProps from './BlogProps'
import BlogData from './Data/MotivationData'
import AOS from 'aos';
import 'aos/dist/aos.css';

const motivation = () => {
    AOS.init();
    return (
        <div className="App Motivation">
            <div className="container">
                <div className="info py-5" data-aos="fade-up" data-aos-duration="2000">
                    <h3 className="top-info">Motivasiyanızı artıracaq ən yaxşı 8 film.</h3>
                    <p>Bizi güldürür, ağladır, hətta şənləndirirlər! Hamımızın motivasiyaya ehtiyacı var. Müasir dövrdə insan rəqabətlə təkbaşına məşğul olanda bəzən cəhd etməkdən yorulur və imtina etmək istəyir. Ancaq bəzən filmlər özümüzə baxmağımızı təmin edir və tez-tez özləri motivasiya mənbəyi olaraq istifadə edilə bilir. Bu məqsədlə, sizə sıradan olmayan bir siyahı təqdim edirik. Bu 8 film mütləq sizi həvəsləndirəcək, ruhlandıracaq və film bitdikdən sonra belə sizə bağlı qalacaq.</p>
                </div>
                <div className="row" data-aos="zoom-in-up" data-aos-duration="2000">
                    {BlogData.map((val, ind) => {
                        return <BlogProps key={ind} title={val.title} about={val.about} imdb={val.imdb} img={val.img} />
                    })}
                </div>
            </div>
        </div>
    )
}

export default motivation;
